.header {
    position: fixed;
    display: flex;
    top: 0%;
    width: 100vw;
    height: 8vh;
    background: rgba(0, 0, 0, 0.32);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(2.3px);
    -webkit-backdrop-filter: blur(2.3px);
    z-index: 200;
}

.header-left-part {
    display: flex;
    align-items: center;
    gap: 1px;
    justify-content: center;
    width: 50vw;
    height: 8vh;
    cursor: pointer;
}

.logo {
    height: 90%;
    width: 5vw;
}

.logo-name {
    font-size: small;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.623);
    transition: color 0.5s ease;
}

.logo-name:hover {
    color: white;
}

.header-right-part {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50vw;
    gap: 20px;
    height: 8vh;
}

.header-right-part a {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.623);
    transition: color 0.3s ease;
    cursor: pointer;
    font-size: small;
    text-transform: uppercase;
}

.header-right-part a:hover {
    color: white;
}

.active-link {
    color: rgba(255, 0, 0, 0.664) !important;
}