.loader {
    display: flex;
    /* flex-direction: column; */
    background-color: #1C1C1C;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    position: relative;
}

.neon-ring {
    position: absolute;
    width: 200px;
    height: 200px;
    border: 4px solid rgba(255, 0, 0, 0.589);
    border-radius: 50%;
    box-sizing: border-box;
    animation: neon 1.5s infinite linear;
    z-index: 100;
}

@keyframes neon {
    0% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(1.1);
        opacity: 0.7;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

.loader-image {
    width: 15vw;
    /* Adjust the width of the image */
    height: 15vh;
    /* Adjust the height of the image */
}

.loader-line {
    width: 20px;
    height: 20px;
    background-color: #0ff;
}

.loader-heading {
    position: absolute;
    bottom: 25%;
}

.loader-heading h3 {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 100;
    animation: glow 2s infinite;
}

@keyframes glow {
    0% {
        color: #ffffff96;
        /* Initial color */
        text-shadow: 0 0 10px #ffffff96, 0 0 20px #ffffff96, 0 0 30px #ffffff96;
        /* Initial shadow effect */
    }
    50% {
        color: #ffffff;
        /* Target color (white) */
        text-shadow: 0 0 20px #ffffff, 0 0 30px #ffffff, 0 0 40px #ffffff;
        /* Glow effect at 50% of animation */
    }
    100% {
        color: #ffffff96;
        /* Return to initial color at the end of animation */
        text-shadow: 0 0 10px #ffffff96, 0 0 20px #ffffff96, 0 0 30px #ffffff96;
        /* Initial shadow effect */
    }
}