@import url('https://fonts.googleapis.com/css2?family=Island+Moments&family=Montserrat:wght@300&family=Playfair+Display&family=Poppins:ital,wght@0,100;0,200;0,400;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Road+Rage&family=Roboto:wght@300&family=Urbanist:ital,wght@0,100;0,200;0,300;1,200&display=swap');
.home-main {
    width: 100%;
    height: 100%;
    background-color: #1C1C1C;
    color: white;
}

.home-image-slider {
    width: 99vw;
    height: 100vh;
}

.swiper-slide {
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
}

.swiper-slide-active {
    opacity: 1;
}

.mySwiper {
    position: relative;
    width: 100%;
    height: 99vh;
}

.image-sliding-part img {
    width: 100vw;
}

.swiper-pagination {
    position: absolute;
    top: 50% !important;
    /* background-color: aqua; */
    /* Adjust the distance from the bottom as needed */
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
}

.swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    background: #fff;
    /* Pagination bullet color */
    opacity: 0.5;
    /* Pagination bullet opacity */
    border-radius: 2px !important;
    margin: 0 5px;
    /* Adjust the space between pagination bullets */
    cursor: pointer;
    transition: all 0.3s ease;
}

.swiper-pagination-bullet-active {
    opacity: 1;
    /* Active pagination bullet opacity */
    background: #007bff;
    /* Active pagination bullet color */
}

.slider-text-part {
    position: absolute;
    width: 100%;
    height: 100%;
    margin-top: 10%;
    z-index: 100;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.slider-title {
    font-size: 4.5rem;
    font-weight: 100;
    font-family: 'Montserrat', sans-serif;
}

.slider-headings {
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 20px;
    color: white;
}

.slider-headings p {
    font-size: 1.4rem;
    letter-spacing: 2px;
    font-family: 'SF Pro Regular';
}

.text-line {
    height: 1px;
    width: 150px;
    background-color: white;
    margin-bottom: 20px;
}

.about-title h3,
.about-paragraph p {
    transition: all 0.3s ease;
    /* Add this line to enable smooth transitions */
    /* your other styles */
}

.about-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.about-title {
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 7px;
    margin-bottom: 3%;
}

.about-title h3 {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 100;
    color: #ffffff96;
}

.about-paragraph {
    width: 70vw;
    display: flex;
    flex-direction: column;
    gap: 50px;
}

.about-paragraph p {
    font-size: 1.5rem;
    font-weight: 100;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
}

.para3 {
    color: rgba(255, 0, 0, 0.5);
    font-weight: bolder !important;
}

.project-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-title {
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 7px;
}

.project-title h3 {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 100;
    color: #ffffff96;
}

.project-categories {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    align-items: center;
    justify-content: center;
    width: 50vw;
}

.project-categories a {
    white-space: nowrap;
    text-decoration: none;
    color: white;
    font-family: 'Montserrat', sans-serif;
    cursor: pointer;
}

.project-categories a.active {
    color: red;
}

.project-main {
    margin-top: 2%;
    width: 75vw;
    height: 100%;
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(250px, auto);
    grid-auto-flow: dense;
    transition: all 0.3s ease;
}

.box {
    overflow: hidden;
    transition: all 0.3s ease;
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    /* Add transition for smooth animation */
}

.each-project-title {
    position: absolute;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 1rem;
    font-weight: bolder;
    color: rgba(190, 14, 14, 0.897);
    z-index: 100;
    opacity: 0; /* Initially hide the title */
    transition: opacity 0.3s ease; /* Add transition for smooth animation */
  }
  
  .box:hover .each-project-title {
    opacity: 1; /* Show the title on hover */
  }

 

.box.viewproject {
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
    cursor: pointer;
    

    /* Add transition for smooth animation */
}


.box img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.processs-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.process-title {
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 7px;
    margin-top: 8%;
}

.process-title h3 {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 100;
    color: #ffffff96;
}

.process-procedure {
    display: flex;
    width: 70vw;
    gap: 5px;
    height: 100%;
}

.pre-design,
.design,
.construct {
    width: 22vw;
    height: 100%;
    padding: 10px 5px;
}

.process-icons:hover {
    background-color: rgba(255, 0, 0, 0.37);
}

.process-icons:hover .process-icon-desc {
    display: flex;
}

.process-icons:hover img {
    display: none;
}

.process-icons {
    width: 100%;
    height: 35vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 5px;
    transition: background-color 0.5s ease;
    background-color: #232323;
}

.process-icon-desc {
    position: absolute;
    width: 50%;
    height: 50%;
    display: none;
    align-items: center;
    transition: display 0.3s ease;
    justify-content: center;
}

.process-icon-desc h3 {
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: large;
    font-weight: 100;
    color: #ffffffb6;
    transition: color 0.3s ease;
}

.process-icons img {
    width: 50%;
    height: 50%;
}

.process-details {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 20px;
    padding: 20px;
    background-color: #232323;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: left;
}

.process-details h3 {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 18px;
    font-weight: 500;
    color: #c2b8b8;
}

.process-steps {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.process-step {
    display: flex;
    gap: 10px;
    align-items: flex-start;
}

.process-step p {
    font-size: 16px;
    color: #c0b8b8;
    letter-spacing: 1px;
    font-family: 'Montserrat', sans-serif;
}

.process-step p:first-child {
    color: #ff5555;
    font-weight: 600;
    margin-right: 10px;
}

.services-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
