.project-display-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    color: white;
    height: 200vh;
    width: 100vw;
    background-color: #1C1C1C;
    /* Set your desired background color */
}

.project-slider {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 8vh;
    align-items: center;
    height: 100vh;
    /* Set your desired width for the slider container */
    /* max-width: 1200px; */
    /* Set the maximum width if needed */
}

.swiper-slide {
    width: 100%;
    height: 70vh;
    /* Set your desired height for the slides */
    object-fit: cover;
}

.mySwiper2 {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.swiper-bottom {
    margin-top: 5px;
    width: 50%;
    gap: 5px !important;
    display: flex;
}

.swiper-button-next,
.swiper-button-prev {
    color: red;
}

.swiper-main-image{
    position: relative;
    display: flex;   
    justify-content: center;
    align-items: center;
}

.project-display-each-title{
    position: absolute;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 3rem;
    font-weight: 100;
    color: rgba(190, 14, 14, 0.705);
    z-index: 100;
}

.swiper-main-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.swiper-bottom .swiper-slide {
    width: 80px;
    /* Width of the slide */
    height: 115px;
    /* Height of the slide */
    object-fit: cover;
    /* Maintain aspect ratio; cover the area completely */
    margin-right: 5px;
    /* Space between slides */
}

.swiper-bottom img {
    width: 100%;
    /* Ensure the image fills the slide container */
    height: 100%;
    /* Ensure the image fills the slide container */
    object-fit: cover;
    /* Maintain aspect ratio; cover the area completely */
    object-position: center;
    /* Center the image within the slide */
}

.project-display-desc {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-display-main-desc {
    width: 80vw;
    height: 100%;
    display: flex;
    margin-top: 1%;
    justify-content: space-between;
    /* background-color: aquamarine; */
}

.project-display-left,
.project-display-right {
    width: 40vw;
    height: 100%;
    display: flex;
    padding: 20px 5px;
    flex-direction: column;
    gap: 10px;
}

.location,
.status,
.project-share {
    display: flex;
}

.pro-brief {
    width: 22vw;
}

.project-display-left p {
    font-family: 'Montserrat', sans-serif;
    /* text-transform: uppercase; */
    letter-spacing: 2px;
    font-weight: 100;
    color: #ffffff96;
}

.project-display-right p {
    font-family: 'Montserrat', sans-serif;
    /* text-transform: uppercase; */
    letter-spacing: 2px;
    font-weight: 100;
    font-size: medium;
    color: #ffffffe7;
}